
import {
	userGetProviderInfo,
	userLoginByUniApp,
	getUserSettingByUniApp,
	getUserInfoByUniApp,
	userLoginByMiChaServer,
} from '@/static/js/authLogin';

export default {
	globalData: {
		openid: '',
		provider: '',
	},
	async onLaunch() {
		try {
			// 获取当前服务端: [weixin, qq, ios..]
			const { provider } = await userGetProviderInfo();
			console.log(provider);
			
			this.globalData.provider = provider[0];
			
			// 获取当前用户登陆code
			const uniAppLoginRes = await userLoginByUniApp(provider[0]);
							
			// 获取当前用户的授权设置
			const { authSetting } = await getUserSettingByUniApp();
						
			// 判断当前用户是否授权
			if (!authSetting['scope.userInfo']) {
				uni.redirectTo({
					url: '/pages/auth/index',
				})
				return;
			}
			
			// 获取当前用户信息
			const { userInfo } = await getUserInfoByUniApp();
			
			// 当前用户登陆米茶服务器
			const miChaServerRes = await userLoginByMiChaServer(uniAppLoginRes.code, userInfo);
			
			this.globalData.openid = miChaServerRes.data.data.result.openid;
			
			// 判断当前用户是否需要绑定手机号码
			if (miChaServerRes.data.data.result.needBind) {
				uni.redirectTo({
					url: '/pages/bind/index',
				})
			}
			
		} catch(err) {
			console.error(err);
		}
	},
	methods: {
		auth() {
			
		}
	}
};
